  .cto {
    position: relative;
    color: #ffffff;
    margin: 20px;
    padding: 19px 22px;
    transition: all 0.2s ease;
    background-color: transparent;
    border: transparent;
    cursor: pointer;
  }
  .cto:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    border-radius: 28px;
    background: rgb(0, 179, 211);
    width: 56px;
    height: 56px;
    transition: all 0.3s ease;
  }
  .cto span {
    position: relative;
    font-size: 16px;
    line-height: 18px;
    font-weight: 900;
    letter-spacing: 0.25em;
    text-transform: uppercase;
    vertical-align: middle;
  }
  .cto svg {
    position: relative;
    top: 0;
    margin-left: 10px;
    fill: none;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke: rgb(255, 255, 255);
    stroke-width: 2;
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
    transition: all 0.3s ease;
  }
  .cto:hover:before {
    width: 100%;
    background: rgb(0, 179, 211);
  }
  .cto:hover svg {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .cto:active {
    -webkit-transform: scale(0.96);
            transform: scale(0.96);
  }
}

